/**
data box style
**/
.data-box {
  border: 2px solid #032d60;
  -webkit-box-shadow: #07417a 0px 0px 10px;
  -moz-box-shadow: #07417a 0px 0px 10px;
  box-shadow: inset 0 0 30px #07417a;
  position: relative;
  margin-top: 10px;
  margin-bottom: 20px;
}

.data-box1 {
  border: 2px solid #f36c11;
  -webkit-box-shadow: #f19a48 0px 0px 10px;
  -moz-box-shadow: #f19a48 0px 0px 10px;
  box-shadow: inset 0 0 30px #f19a48;
  position: relative;
  margin-top: 10px;
  margin-bottom: 20px;
}

.box-back {
  height: 600px;
  background-image: -webkit-radial-gradient(
    250px 250px at 50% 50%,
    hsla(209, 99%, 32%, 28),
    hsla(228, 100%, 0%, 0)
  );
  background-image: radial-gradient(
    250px 250px at 50% 50%,
    hsla(209, 99%, 32%, 28),
    hsla(228, 100%, 0%, 0)
  );
}

.topL {
  width: 20px;
  height: 20px;
  border-top-width: 2px;
  border-top-color: #26c6f0;
  border-top-style: solid;
  border-left-width: 2px;
  border-left-color: #26c6f0;
  border-left-style: solid;
  position: absolute;
  top: -2px;
  left: -2px;
}
.topR {
  width: 20px;
  height: 20px;
  border-top-width: 2px;
  border-top-color: #26c6f0;
  border-top-style: solid;
  border-right-width: 2px;
  border-right-color: #26c6f0;
  border-right-style: solid;
  position: absolute;
  top: -2px;
  right: -2px;
}
.bottomL {
  width: 20px;
  height: 20px;
  border-bottom-width: 2px;
  border-bottom-color: #26c6f0;
  border-bottom-style: solid;
  border-left-width: 2px;
  border-left-color: #26c6f0;
  border-left-style: solid;
  position: absolute;
  bottom: -2px;
  left: -2px;
}
.bottomR {
  width: 20px;
  height: 20px;
  border-bottom-width: 2px;
  border-bottom-color: #26c6f0;
  border-bottom-style: solid;
  border-right-width: 2px;
  border-right-color: #26c6f0;
  border-right-style: solid;
  position: absolute;
  bottom: -2px;
  right: -2px;
}

.data-title-left,
.data-title-right {
  color: #105eda;
  font-family: "";
  font-size: 20px;
}
.data-title {
  background-color: #01095E;
  width: 180px;
  margin: -18px auto 0 auto;
  color: #83c7e3;
  font-size: 20px;
  text-align: center;
}
.data-title span {
  margin: 0 10px;
}

.box-chart {
  width: 100%;
  margin: 10px auto;
}


/**
ksh box style
**/
@font-face{
	font-family: yjsz;
	src:url('../assets/fonts/yjsz.TTF'),
		url('../assets/fonts/yjsz.eot'); /* IE9+, */
  }
  
.group-kshbox{
  margin-top:5px;
  margin-bottom:20px;
}
.kshbox{
  height: 125px;
}

.kshbox div.box{
  height: 100%;
  background: url(../assets/img/kshbox.png) no-repeat;
  background-size: 100% 100%;
}

.kshbox div h3{
  color: #fff;
  font-size: 16px;
  padding: 16px 0 0 20px;
}

.kshbox div p{
  width: 33%;
    float: left;
    line-height: 35px;
    color: #20dbfd;
    text-shadow: 0 0 25px #00d8ff;
    font-size: 46px;
    font-family: 'yjsz';
    text-align: right;
}

.kshbox div .smil{
  width: 67%;
  height: 60px;
  float: left;
  padding-top: 5px;
}

.kshbox div .smil span{
  display: block;
  line-height: 20px;
  text-align: left;
  color: #fff;
  padding-left: 9px;
}
.kshbox div .smil span.xd{
  color: #12fe81;
}
.kshbox div .smil span.sz{
  color: #fe3e12;
}



/**
skin-2
*/
.skin-2 .data-box {
  border: 2px solid #56460c;
  -webkit-box-shadow: #07417a 0px 0px 10px;
  -moz-box-shadow: #07417a 0px 0px 10px;
  box-shadow: inset 0 0 30px #07417a;
}

.skin-2 .data-title {
  background-color: #060d19;
  color: #fff;
}

.skin-2 .topL {
  border-top-color: #f37a1f;
  border-left-color: #f37a1f;

}
.skin-2 .topR {
  border-top-color: #f37a1f;
  border-right-color: #f37a1f;
}
.skin-2 .bottomL {
  border-bottom-color: #f37a1f;
  border-left-color: #f37a1f;
}
.skin-2 .bottomR {
  border-bottom-color: #f37a1f;
  border-right-color: #f37a1f;
}

.skin-2 .topbnt_left li.active a {
  color: #f37a1f;
}

.skin-2 .topbnt_right li.active a {
  color: #f37a1f;
}

canvas {
  position: absolute;
  top: 0;
  left: 0;
}